html{
    background: #A8ECC5 url(https://catherineasquithgallery.com/uploads/posts/2021-02/1612706197_14-p-zelenii-fon-odnotonnii-gradient-15.jpg);
    height: 100%;
    line-height: 1.5;
    font-size: 17px;
    text-align: center;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    box-sizing: border-box;
}

h1{
    margin-bottom: 20px;
}

h2{
    margin-top: 0;
}

.App {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 40px 40px 10px 40px;
}

input {
    padding: 10px 15px;
    outline: none;
    border-radius: 5px;
    border: 1px solid #199d54;
}

button {
    padding: 10px 15px;
    outline: none;
    border-radius: 5px;
    border: 1px solid #199d54;
    font-weight: 600;
}

button:not(button:disabled) {
    background-color: #48e88c;
    color: #212121;
}

button:hover:not(button:disabled){
    background-color: #8dfdba;
    cursor: pointer;
}

.task{
    width: 46%;
    padding: 30px;
    border: 1px solid #38D37A;
    border-radius: 10px;
    background: #fff;
    margin-bottom: 30px;
}
